import React, { useEffect, useState } from "react";

const Photo = ({ id, name }) => {
  const [bolb, setBolb] = useState(null);
  const [error, setError] = useState(false);
  const getphoto = async () => {
    try {
      const QURL = process.env.DATA_URL + "addphoto/getphoto/" + id;
      let get = await fetch(QURL);
      if (get.ok) {
        let blob = await get.blob();
        let src = URL.createObjectURL(blob);
        setBolb(src);
      }
    } catch (err) {
      console.log(err);
      setError(true);
    }
  };
  useEffect(() => {
    getphoto();
  }, [id]);
  return (
    <>
      {bolb && <img src={bolb} alt={name} className="w-100" />}
      {error && <h2> Wron </h2>}
    </>
  );
};

export default Photo;
